<template>
  <div id="etop_box">
    <div class="etop-box-wrapper">
      <div
        :id="`mars3d-container${mapKey}`"
        :class="[
          'mars3d-container',
          customClass,
          { 'mars3d-container-compare-rh': compare },
        ]"
        :style="styleObject"
      ></div>
    </div>
    <!-- 地图蒙板 -->
    <img v-if="isMapMask" class="mapMask" :src="maskUrl" />

    <div :class="[searchStatus ? 'activeTools' : 'tools' , isMenuBox ? 'toggle':'untoggle' ]"  id="region_and_search" >
      <!-- 区域选择三级菜单组件 -->
      <region-switcher
        v-show="isRegionSwitcher"
        :isSubBoundary="isSubBoundary"
        :reset-flag="resetFlag"
        :devMode="this.devMode"
        :publicPath="this.publicPath"
        :showMenuGrade="showRegionSwitcherMenuGrade"
        :isInitFullView="this.isInitFullView"
        @isMenuBoxFun="isMenuBoxFun"
        @clickRegionSwitcher="clickRegionSwitcher"
        @regionBack="regionBack"
        :regionSwitcherSelect="regionSwitcherSelect"
        ref="regionSwitcherRef"
      />
      <geocoder-search
        @changeSearchStatus="changeSearchStatus"
        @updateRegionSwitcher="updateRegionSwitcher"
      />
    </div>

    <!-- 比例尺 -->
    <DistanceLegendTool
      v-show="controlTools.distanceLegendTool.enable"
      ref="DistanceLegendToolRef"
    />

    <!-- 工具箱和垂直工具栏 -->
    <ToolBox
      v-show="isToolBar"
      ref="ToolBoxRef"
      @fullScreen="fullScreen"
      @exitFullscreen="exitFullscreen"
      @reset="reset"
      @setPitch="setPitch"
      @updateRegionSwitcher="updateRegionSwitcher"
      @toggleFullService="toggleFullService"
      :outsideTool="outsideTool"
      :boxToolOption="boxToolOption"
      :baseLayerName="baseLayerName"
      :isToolBoxShow="isToolBoxShow"
    ></ToolBox>

    <BaseLayerSwitcher1
      v-show="isBaseLayer"
      :curLayerName="curLayerName"
      :isSSMap="isSSMap"
      :disabledSSMap="disabledSSMap"
      :hiddenSSMap="hiddenSSMap"
      :baseLayerArr="baseLayerArr"
      :SzView="this.isInitFullView"
      @flySSorSZ="flySSorSZ"
    />

    <!-- 图层控制树 右侧 -->
    <LayerControl
      ref="LayerControlRef"
      :isLayerControlShow="isLayerControlShow"
    ></LayerControl>
    <!-- 业务图层 -->
    <TaskLayers
      v-show="controlTools.isShowTaskLayer"
      ref="TaskLayersRef"
    ></TaskLayers>

    <!-- <region-site
      v-show="isRegionSite"
      :regionSelect="selectedRegionObj"
      :devMode="this.devMode"
      :publicPath="this.publicPath"
      :levelSelect="levelSelect"
      :regionlevel="regionlevel"
      :backRegion="backRegion"
      @changeRegionlevel="changeRegionlevel"
      ref="RegionSiteRef"
    ></region-site> -->
  </div>
</template>

<script>
// 使用免费开源版本
import "mars3d/dist/mars3d.css";
import * as mars3d from "mars3d";
//开发引入
//import EtopMap from "packages/etop3d/index";

//开发引入
//import { EtopMap } from "packages/etop3d/index.etopmap";
//npm包引入
import {EtopMap} from 'etop3d'
import RegionSwitcher from "./regionSwitcher/RegionSwitcher.vue";
import DistanceLegendTool from "./tools/DistanceLegendTool.vue";
import GeocoderSearch from "./tools/GeocoderSearch.vue";
import LayerControl from "./layersControl/LayerControl.vue";
import BaseLayerSwitcher1 from "./baseLayer/BaseLayerSwitcher.vue";
import TaskLayers from "./layersControl/TaskLayers.vue";
import ToolBox from "./tools/Toolbox.vue";

//import RegionSite from "./regionSwitcher/RegionSite.vue";

/**
 * @module Mars3dViewer
 * @desc Mars3dViewer组件作为一个基础初始化场景的组件，由EtopMap类作为入口控制，以及提供其他组件的方法绑定
 * @vue-prop {String} devMode 开发环境状态设置,用于确定开发请求接口地址和屏幕适配，确保值为字符串development（默认生产环境）或者production（线上环境）
 * @vue-prop {String} publicPath 开发和部署的公共路径设置，'如http://localhost:3000/publicPath/
 * @vue-prop {String} url 初始化配置参数
 * @vue-prop {String} mapKey 地图唯一性标识
 * @vue-prop {Object} options 自定义参数
 * @vue-prop {Boolean} compare 是否分屏显示,默认为false
 * @vue-prop {Boolean} appendToBody 是否插入到body元素上,默认为false
 * @vue-prop {String} customClass 自定义css类名
 * @vue-prop {Object} maskOptions 行政区蒙版设置
 * @vue-prop {Boolean} isOuterMask 是否显示深圳外部蒙版,默认true显示
 * @vue-prop {Boolean} isXZQMask 是否显示蓝色遮盖层,true
 * @vue-prop {Boolean} isSSMap 是否显示深汕小地图,默认为true
 * @vue-prop {Boolean} isRegionSwitcher 是否显示区域选择三级菜单控件,默认为true
 * @vue-prop {Boolean} isBaseLayer 是否显示底图切换控件，默认为true
 * @vue-prop {Boolean} isToolBar 是否显示工具条控件，默认为true
 * @vue-prop {Object} controlTools 是否显示其他控件
 * @vue-prop {Boolean} isgdImage 是否显示广东背景图片
 * @vue-prop {Boolean} isRegionSite 是否显示区域单选框
 * @vue-prop {Boolean}  regionlevel 区域单选框  1 区， 2 街道  3 社区 4 网格
 * @vue-prop {Number} [showRegionSwitcherMenuGrade=3] showRegionSwitcherMenuGrade 控制显示的行政区层级 默认3级

 * @vue-prop {String} regionSwitcherSelect 行政区默认选中区域,默认深圳市
 * @vue-prop {String} curLayerName 当前使用的底图,默认"电子地图"
 * @vue-prop {Boolean} isTerrainOn 是否开启地形,默认false
 * @vue-prop {Boolean} isInitFullView 是否切换默认显示全深圳、深汕视角,默认true
 * @vue-prop {Array} baseLayerArr 图层名称配置
 * @vue-prop {Boolean} outsideTool 设置侧边工具栏  ['放大','缩小','全屏','重置',"全量服务菜单","收藏"]
 * @vue-prop {Boolean} boxToolOption 设置下拉工具栏 ['测距','场景控制']
 * @vue-prop {Number} externalScaling=1 外部缩放比例，用于大屏适配,默认为1

 * @vue-data {Boolean} searchStatus - 是否检索
 * @vue-data {String} maskUrl - 地图蒙版图片地址
 * @vue-data {Boolean} resetFlag -  监听重置

 * @vue-data {String} baseLayerName -  当前使用的底图名
 * @vue-data {Object} styleObject -  绑定的样式对象
 * @vue-data {Number} scale -  缩放系数
 * @vue-data {Boolean} isMenuBox -  是否显示行政区下拉菜单栏

 * @vue-computed {Array.<String>} barList - A list of bar
 * @vue-computed {String} message A message
 * @vue-event {Number} increment - Emit counter's value after increment
 * @vue-event {Number} decrement - Emit counter's value after decrement
 */

export default {
  name: "Mars3dViewer",
  components: {
    RegionSwitcher,
    DistanceLegendTool,
    LayerControl,
    ToolBox,
    BaseLayerSwitcher1,
    GeocoderSearch,
    TaskLayers,
  },
  props: {
    //开发环境
    devMode: {
      type: String,
      default: () => {
        return import.meta.env.VITE_APP_ENV;
      },
    },
    //文件公共路径
    publicPath: {
      type: String,
      default: "",
    },
    // 初始化配置参数
    url: {
      type: String,
      default: function () {
        let str = "/map3d/config.json";
        return str;
      },
    },
    // 地图唯一性标识
    mapKey: {
      type: String,
      default: "",
    },
    // 自定义参数
    options: {
      type: Object,
      default: () => {
        return {};
      },
    },
    // 是否分屏显示
    compare: {
      type: Boolean,
      default: false,
    },
    // 是否插入到body元素上
    appendToBody: {
      type: Boolean,
      default: false,
    },
    // 自定义css类名
    customClass: {
      type: String,
      default: "",
    },
    // 蒙版颜色
    maskOptions: {
      type: Object,
      default: () => {
        return {
          isShowWark: true, //是否带高度
          isAddPrimitives: true,
          styleOptions : {
            zIndex: 0,  // 图层层级
            height: 3000, //边界墙高度
            fill:true,  //是否填充面
            clampToGround: false, //是否贴地
            opacity: 0.95, //面透明度
            outlineStyle: {
              color: "#0092FF", //边界线颜色
              width: 3, //边界线宽度
              opacity: 1,  //边界线透明度
            },
          }
        };
      },
    },
    // 是否显示深圳外部蒙版
    isOuterMask: {
      type: Boolean,
      default: true,
    },
    // 是否显示蓝色遮盖层
    isXZQMask: {
      type: Boolean,
      default: true,
    },
    // 是否显示深汕小地图
    isSSMap: {
      type: Boolean,
      default: true,
    },
    // 是否显示区域选择三级菜单控件
    isRegionSwitcher: {
      type: Boolean,
      default: true,
    },
    // 是否显示底图切换控件
    isBaseLayer: {
      type: Boolean,
      default: true,
    },
    // 是否显示工具条控件
    isToolBar: {
      type: Boolean,
      default: true,
    },
    // 是否显示
    controlTools: {
      type: Object,
      default: () => ({
        isShowTaskLayer: false, //是否显示业务图层控制器
        SSMap: { enable: true },
        regionSwitcher: { enable: true },
        baseLayer: { enable: true },
        toolBar: {
          enable: true,
          isGeoCoderSearchEnable: true,
          isFullScreenEnable: true,
          isResetLocationEnable: true,
          isHomeEnable: true,
          is3DEnable: true,
          isPitchFixed: false,
        },
        distanceLegendTool: { enable: true }, // 是否显示比例尺
        zoomTool: { enable: true },
      }),
    },
    // 是否显示广东背景图
    isgdImage: {
      type: Boolean,
      default: true,
    },
    // 是否需要区域单选点
    isRegionSite: {
      type: Boolean,
      default: false,
    },
    // 是否需要地图蒙版，初始为圆形
    isMapMask: {
      type: Boolean,
      default: true,
    },
    //控制显示的行政区层级 默认3级
    showRegionSwitcherMenuGrade: {
      type: Number,
      default: 3,
    },
    //是否禁用深汕底图点击切换
    disabledSSMap: {
      type: Boolean,
      default: false, //默认不禁用
    },
    //是否隐藏深山小地图
    hiddenSSMap: {
      type: Boolean,
      default: false, //默认不隐藏
    },
    // 行政区默认选中区域
    regionSwitcherSelect: {
      type: String,
      default: "深圳市",
    },
    // 当前使用的底图
    curLayerName: {
      type: String,
      default: "电子地图",
    },
    //是否开启地形
    isTerrainOn: {
      type: Boolean,
      default: false,
    },
    // 是否切换默认显示全深圳、深汕视角
    isInitFullView: {
      type: Boolean,
      default: true,
    },
    baseLayerArr: {
      type: Array,
      default: () => ["电子地图", "卫星影像", "三维白模", "倾斜摄影"],
    },
    outsideTool: {
      type: Array,
      default: () => {
        return ["放大", "缩小", "全屏", "重置", "全量服务菜单", "收藏"];
      },
    },
    boxToolOption: {
      type: Array,
      default: () => {
        return ["测距", "场景控制"];
      },
    },
    isToolBoxShow:{
      type:Boolean,
      default:true,
    },
    regionlevel: {
      type: Number,
      default: 1,
    },
    // 显示次级边界
    isSubBoundary: {
      type: Boolean,
      default: false,
    },
    // 外部缩放比例
    externalScaling: {
      type: Number,
      default: 1,
    },
  },
  data() {
    return {
      searchStatus: false,
      // 地图蒙版图片地址
      maskUrl: new URL("../../assets/images/etop/mask.png", import.meta.url)
        .href,
      // 监听重置
      resetFlag: false,
      isSZ: true, // 标识是否在深圳
      baseLayerName: "电子地图", // 当前使用的底图名
      styleObject: {},
      scale: 1,
      isMenuBox: false,
      // isInitFullView: true,  //是否切换默认显示全深圳、深汕视角
      baseLayers: null,
      isLayerControlShow: false,
      selectedRegionObj: null, //选择的行政区属性对象
      levelSelect: "", //行政区所选级别
      backRegion: "", //行政区放回上一级
      currentRigon: "深圳市",
    };
  },
  computed: {
    // isMenuBoxHeight() {
    //   return this.isMenuBox
    //     ? {
    //         height: "calc(100% - 40px - 60px - 170px)",
    //         minHeight: "186px",
    //         maxHeight: "436px",
    //         zIndex: "99999999999",
    //       }
    //     : "height: auto";
    // },
  },
  created() {},
  watch: {},
  mounted() {
    window.isSZ = true;
    window.devMode = this.devMode;
    window.publicPath = this.publicPath;

    if (this.appendToBody) {
      document.body.appendChild(this.$el);
    }
    // 接收外部缩放系数，绑定到this.scale，同时生成合适的style
    this.scale = this.externalScaling;
    this.styleObject = this.getScaleStyle(this.scale);

    if (this.mapKey) {
      this.initMars3d(this.options);
    } else {
      mars3d.Resource.fetchJson({ url: this.url }).then((data) => {
        this.initMars3d(data.map3d); // 构建地图
      });
    }
  },
  unmounted() {
    if (this[`map${this.mapKey}`]) {
      let gl = etopMap.map.viewer.scene.context._originalGLContext;
      this[`map${this.mapKey}`].destroy();
      gl.getExtension("WEBGL_lose_context").loseContext();
      gl = null;
    }
  },

  methods: {
    changeSearchStatus() {},
    toggleFullService(val) {
      this.isLayerControlShow = val;
    },
    isMenuBoxFun(val) {
      this.isMenuBox = val;
    },
    toggleSearchStatus(val) {
      this.searchStatus = val;
    },
    // 根据外部scale值，计算缩放系数
    getScaleStyle(scale) {
      let num = (-100 * (scale - 1)) / 2;
      let styleObject = {
        transform: `scale(${1 / scale}) translate(${num}%, ${num}%)`,
        WebkitTransform: `scale(${1 / scale}) translate(${num}%, ${num}%)`,
        width: `${scale * 100}%`,
        height: `${scale * 100}%`,
      };
      return styleObject;
    },
    initMars3d(options) {
      this.options["devMode"] = this.devMode;
      this.options["publicPath"] = this.publicPath;
      let mapOptions = {
        ...options,
        ...this.options,
      };

      // 创建三维地球场景
     
      let map = new mars3d.Map(`mars3d-container${this.mapKey}`, mapOptions);
      this.map = map;

      //设置滚轴
      if (this.scale > 1) {
        this.map.scene.screenSpaceCameraController.zoomFactor = 3 * this.scale;
      }
      let bigScale = this.scale
      // 此处将外部的scale值，通过bigScale传入了EtopMap对象中
      mapOptions.bigScale = bigScale;
      const etopMap = new EtopMap(map, mapOptions);
      this.etopMap = etopMap;
      window.etopMap = etopMap;
      window.mars3d = mars3d;
      window.Cesium = mars3d.Cesium;
      // 删除火星logo，删除第一个viewport
      let primitivesCollection = map.viewer.scene.primitives._primitives;
      let viewport = null;
      for (let i = 0; i < primitivesCollection.length; i++) {
        if (viewport) {
          break;
        }
        if (primitivesCollection[i] instanceof Cesium.ViewportQuad) {
          viewport = primitivesCollection[i];
        }
      }
      viewport && viewport.destroy();
      // 设置固定光照，避免因时间导致模型明暗不一
      map.fixedLight = true;
      // 设置相机最大缩放距离
      //map.viewer.scene.screenSpaceCameraController.maximumZoomDistance = 100000
      // 设置地球背景色，因为加载了百度地图，所以没必要再设置了
      // etopMap.setGlobeBaseColor('#01041A', 0.8)

      // // 单张全球影像图片
      //  const globeImage = new mars3d.layer.ImageLayer({
      //    //url: this.$STATIC_URL + '/images/etop/baselayer/singleBlue.jpg',
      //    url: require('../assets/images/etop/baselayer/night.jpg'),
      //    rectangle: { xmin: -180, xmax: 180, ymin: -90, ymax: 90 },
      //    alpha: 0.7
      //   })
      //  map.addLayer(globeImage)

      // 单张广东范围影像图片
      if (this.isgdImage) {
        const gdImage = new mars3d.layer.ImageLayer({
          url: new URL("../../assets/images/etop/gdImage.jpg", import.meta.url).href,
          rectangle: {
            xmin: 108.28125,
            xmax: 120.9375,
            ymin: 18.051733095556166,
            ymax: 27.132125784374054,
          },
        });
        map.addLayer(gdImage);
      }

      this.$refs.LayerControlRef.init(map);
      this.$refs.ToolBoxRef.init(map);
      this.$refs.TaskLayersRef.init(map);
      //this.isRegionSite && this.$refs.RegionSiteRef.init(map);
      this.isSubBoundary && this.$refs.regionSwitcherRef.init();

      //  this.$refs.BaseLayerRef.init(map)

      // 初始化比例尺
      if (this?.controlTools?.distanceLegendTool?.enable !== false)
        this.$refs.DistanceLegendToolRef.init(map);

      // 添加行政区图层用于遮罩
      if (this.isXZQMask) {
        this.etopMap.addXzqLayer(this.maskOptions);
      }
      //加载底图
      this.etopMap.gzLayer.loadBaseLayer(this.baseLayerName);
      // 抛出加载完成事件
      this.$emit("onload", etopMap);
    },

    exitFullscreen() {
      this.$emit("exitFullscreen");
    },
    // 全屏操作
    fullScreen() {
      this.$emit("fullScreen");
    },
    // 转换到外部3D地图
    threeDim() {
      this.$emit("threeDim");
    },
    // 回到当前位置
    resetLocation() {
      this.$emit("resetLocation");
    },
    filterChange(layerName) {
      this.$emit("filterChange", layerName);

      this.baseLayerName = layerName;
      if (this.isSZ) {
        this.toogleSS();
      } else {
        this.toogleSZ();
      }
    },
    // 重置
    reset() {
      this.$emit("reset");
      if (this.isInitFullView) {
        this.isSZ = true;
        this.etopMap.resizeViewPort(this.isSZ);
      } else {
        //options  配置的初始化视角
        this.map.flyHome({
          duration: 1,
        });
      }
      this.resetFlag = !this.resetFlag;
    },
    //flySSorSZ 深汕切换 flag:true 显示深圳 ，反之 深汕
    flySSorSZ(flag) {
      this.$emit("flySSorSZ", flag);
    },
    /* changeRegionlevel(level) {
      this.$emit("changeRegionlevel", level);
    }, */
    regionBack(e, backRegion) {
      this.levelSelect = e;
      this.backRegion = backRegion;
  
      this.$emit("regionBack", e, backRegion);
    },
    clickRegionSwitcher(e,r) {
      console.log("------e", e);
      this.selectedRegionObj = e;

      this.$emit("clickRegionSwitcher", e,r);
    },
    updateRegionSwitcher(res) {
      this.$refs.regionSwitcherRef?.retrieveData(res?.properties?.sq_name);
    },
    setPitch(val) {
      //设置垂直视角
      this.etopMap.map.setPitch(-90);
      //垂直视角是否可用
      this.etopMap.scene.screenSpaceCameraController.enableTilt = val;
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
#etop_box {
  position: relative;
  height: 100%;
  //background: #000;
  .etop-box-wrapper {
    position: relative;
    height: 100%;
    overflow: hidden;

    .mars3d-container {
      height: 104%;
      position: static;
    }

    //今日深圳加
    .today-mars3d-container {
      :deep(.mars3d-divGraphic) {
        pointer-events: none !important;
      }
    }
  }

  .region-site {
    position: absolute;
    top: 10px;
    left: 50px;
    display: flex;
    justify-content: center;
  }

  .tools {
    position: absolute;
    top: 10px;
    right: 270px;
    display: flex;
    justify-content: center;
  }

  .activeTools {
    position: absolute;
    top: 0;
    right: 330px;
    display: flex;
    justify-content: center;
  }

  .toggle {
    height: calc(100% - 40px - 60px - 170px);
    min-height: 186px;
    max-height: 436px;
    z-index: "9999",
  }

  .untoggle {
    height: auto
  }

  .mapMask {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
  }

  .shenshan_box {
    position: absolute;
    z-index: 99;
    right: 0;
    bottom: 0;
    width: 153px;
    height: 153px;
    cursor: pointer;

    img {
      width: 100%;
      height: 100%;
    }
  }

  .shenshan_box.hide {
    display: none;
  }

  .shenshan_box.disabled {
    cursor: no-drop;
  }

  .mars3d-container {
    > :deep(.mars3d-overviewMap > .cesium-viewer) {
      // 为了解决鹰眼控件 水印问题
      height: 115%;
    }

    // 修改默认比例尺样式
    :deep(.mars3d-distance-legend) {
      opacity: 0;
    }

    :deep(.cesium-viewer-geocoderContainer) {
      position: absolute;
      right: 180px;
      height: 30px;
      z-index: 100;
      top: 14px;
      background-color: rgba(11, 36, 68, 0.8);
      border: 1px solid rgba(84, 181, 255, 0.3);
      box-sizing: border-box;

      svg {
        width: 28px;
        height: 28px;
        fill: rgb(84 181 255);
        background-color: rgba(11, 36, 68, 0.8);
      }
    }

    :deep(.cesium-geocoder-searchButton) {
      width: 28px;
      height: 28px;
