//获取本地区数据
export function getRegionData(filePath) {
  console.log(filePath)
  if(filePath===undefined||filePath===null){
    filePath=""
  }
  let url=`${window.location.origin}${filePath}/map3d/region/szxzq5.geojson`
  return axios({
    url: url,
    method: 'get',
  })
}

