<template>
  <div>
    <div class="container" id="baseLayerSwitcher">
      <div class="menu" @click="toggle">
        {{ baseLayerName }}
        <span class="down_arrow"></span>
      </div>
      <div class="menu_box" v-show="isToggle">
        <div
          v-for="(item, index) in baseLayerArr"
          :key="index"
          class="item_menu"
          :class="{ item_active: baseLayerName === item }"
          @click="clickItem(item)"
        >
          <img :src="baseConfig[item].iconUrl" alt />
          <div class="label">{{ item }}</div>
        </div>
      </div>
    </div>

    <!--深汕小底图-->
    <div
      class="shenshan_box"
      v-show="isSSMap"
      @click="flySSorSZ"
      :class="[hiddenSSMap ? 'hide' : '', disabledSSMap ? 'disabled' : '']"
    >
      <img v-if="this.isSzView" :src="baseConfig[baseLayerName].ssUrl" />
      <img v-else :src="baseConfig[baseLayerName].szUrl" />
    </div>
  </div>
</template>
<script>
import { config } from "./baseConfig";

/**
 * @module BaseLayerSwitcher
 * @desc BaseLayerSwitcher组件,地图类型切换功能，含电子地图，卫星影像，三维白模，倾斜摄影4种类型
 */
export default {
  props: {
    // 是否切换默认显示全深圳、深汕视角
    SzView: {
      type: Boolean,
      default: true,
    },
    // 底层默认选中
    curLayerName: {
      type: String,
      default: "电子地图",
    },
    // 底图参数
    baseLayerArr: {
      type: Array,
      default: () => ["电子地图", "卫星影像", "三维白模", "倾斜摄影"],
    },
    // 是否显示深汕小地图
    isSSMap: {
      type: Boolean,
      default: true,
    },
    //是否禁用深汕底图点击切换
    disabledSSMap: {
      type: Boolean,
      default: false, //默认不禁用
    },
    //是否隐藏深山小地图
    hiddenSSMap: {
      type: Boolean,
      default: false, //默认不隐藏
    },
  },

  data() {
    return {
      isToggle: false,
      baseLayerName: this.curLayerName,
      baseConfig: config, //底图图片
      isSzView: true,
    };
  },
  watch: {
    curLayerName(val) {
      this.clickItem(val);
    },
  },
  methods: {
    clickItem(name) {
      this.baseLayerName = name;
      //收起
      this.isToggle = false;

      //加载底图
      window.etopMap.gzLayer.loadBaseLayer(name);
    },
    toggle() {
      this.isToggle = !this.isToggle;
    },
    flySSorSZ() {
      this.isSzView = !this.isSzView;
      //90°飞行到指定范围(深圳或深汕) 内部处理
      if(this.$parent.isInitFullView){
        window.etopMap.resizeViewPort(this.isSzView);
      }
      this.$emit("flySSorSZ",this.isSzView);
    },
  },
};
</script>
<style scoped lang="less">
.container {
  position: absolute;
  top: 10px;
  right: 160px;
  width: 100px;
  cursor: pointer;
}

.menu {
  width: 100px;
  // height: 32px;
  padding: 5px 5px 5px 8px;
  color: #54b5ff;
  border: 1px solid rgba(84, 181, 255, 0.3);
  background-color: rgba(11, 36, 68, 0.8);

  .down_arrow {
    position: absolute;
    right: 12px;
    top: 12px;
    width: 0;
    height: 0;
    border-left: 8px solid transparent;
    border-right: 8px solid transparent;
    border-top: 8px solid #54b5ff;
  }
}

.menu_box {
  margin-top: 5px;
  display: flex;
  flex-direction: column;
  transition: all 1s;
  flex: 1;
  border: 1px solid rgba(84, 181, 255, 0.3);
  background-color: rgba(84, 181, 255, 0.1);
  white-space: nowrap;

  .item_menu {
    width: 70px;
    margin: 7px auto 0px auto;
    cursor: pointer;

    img {
      width: 70px;
      height: 45px;
    }

    .label {
      color: #fff;
      text-align: center;
    }
  }

  .item_active {
    img {
      border: 1px solid #beb60e;
    }

    .label {
      color: #beb60e;
    }
  }
}

.shenshan_box {
  position: absolute;
  z-index: 99;
  right: 0;
  bottom: 0;
  width: 153px;
  height: 153px;
  cursor: pointer;

  img {
    width: 100%;
    height: 100%;
  }
}
</style>
