<template>
<div class="ytools">
  <div class="toolbox" v-if="isToolBoxShow">
    <div class="menu" @click="toggle">
         工具栏
      <span class="down_arrow"></span>
    </div>
    <div class="menu_box" v-show="isToggle"> 
      <div  v-for="item in toolboxOption"  :key="item.value"  :title ="item.value"  @click="toolChange(item.value)"> 
        <img :src="item.img" alt  class="menu_item" />
      </div>
    </div>
    <!-- <a-select
      :popper-append-to-body="false"
      class="select-popper"
      placeholder="工具"
      :allowClear="true"
      @select="toolChange"
      :getPopupContainer="triggerNode => triggerNode.parentNode"
    >
     <template #suffixIcon><div class="down_arrow" /></template>
      <a-select-option
        v-for="item in toolOption"
        :title="item.label"
        :key="item.value"
        :label="item.label"
        :value="item.value"
      >
      <div class="label">
       {{item.label}}
      </div>     
      </a-select-option>
    </a-select> -->
  </div>
  <div class="outsidetool">
    <div class="outsideTool" v-for="(item,index) in outsideOption" :key="index" :title="item.label" @click="toolChange(item.value)">
      <img v-if="item.value == '全屏'" :src="disableTool.indexOf('全屏') !==-1 ? item.disableImg : (isFullScreen?item.imgActive:item.img)" alt="">
      <img v-else-if="item.value == '全量服务菜单'" :src="disableTool.indexOf('全量服务菜单') !==-1 ? item.disableImg : (isLayerControlShow?item.imgActive:item.img)" alt="">
      <img v-else :src="disableTool.indexOf(item.value) !==-1 ? item.disableImg : item.img" alt="">
    </div>
  </div>
</div>
</template>

<script>
export default {
  name:'ToolBox',
  data(){
    return {
      isToggle:false,
      isLayerControlShow:false, //全量服务菜单是否显示
      isFullScreen:false,  //是否全屏
      pitchEnabled: true,  //是否允许垂直视角变化
      toolboxOption:[],  //放工具箱
      outsideOption:[],  //放外面
      toolOption:[
          {
            value:'放大',
            label:'放大',
            img:new URL("../../../assets/images/etop/tool/icon_ZoomIn.png",import.meta.url).href,
            disableImg:new URL("../../../assets/images/etop/tool/icon_ZoomIn_disable.png",import.meta.url).href,
          },
          {
            value:'缩小',
            label:'缩小',
            img:new URL("../../../assets/images/etop/tool/icon_ZoomOut.png",import.meta.url).href,
            disableImg:new URL("../../../assets/images/etop/tool/icon_ZoomOut_disable.png",import.meta.url).href,
          },
          {
            value:'全屏',
            label:'全屏',
            imgActive:new URL("../../../assets/images/etop/tool/icon-Collapsefull screen.png",import.meta.url).href,
            img:new URL("../../../assets/images/etop/tool/icon_fullscreen.png",import.meta.url).href,
            disableImg:new URL("../../../assets/images/etop/tool/icon_fullscreen_disable.png",import.meta.url).href,
          },
          {
            value:'重置',
            label:'重置',
            img:new URL("../../../assets/images/etop/tool/icon-home.png",import.meta.url).href,
            disableImg:new URL("../../../assets/images/etop/tool/icon-home_disable.png",import.meta.url).href,
          },
          {
            value:'全量服务菜单',
            label:'全量服务菜单',
            img:new URL("../../../assets/images/etop/tool/icon_map_nor.png",import.meta.url).href,
            imgActive:new URL("../../../assets/images/etop/tool/icon_map_sel.png",import.meta.url).href,
            disableImg:new URL("../../../assets/images/etop/tool/icon_map_disable.png",import.meta.url).href,
          },
          {
            value:'锁定垂直视角',
            label:'锁定垂直视角',
            img:new URL("../../../assets/images/etop/tool/icon-home.png",import.meta.url).href,
            disableImg:new URL("../../../assets/images/etop/tool/icon-home_disable.png",import.meta.url).href,
          },
          {
            value:'测距',
            label:'测距',
            img:new URL("../../../assets/images/etop/tool/icon_ruler.png",import.meta.url).href,
            disableImg:new URL("../../../assets/images/etop/tool/icon_ruler_disable.png",import.meta.url).href,
          },
          {
            value:'场景控制',
            label:'场景控制',
            img:new URL("../../../assets/images/etop/tool/sceneControl.png",import.meta.url).href,
            imgActive:new URL("../../../assets/images/etop/tool/sceneControl_disabled.png",import.meta.url).href,
            disableImg:new URL("../../../assets/images/etop/tool/sceneControl_disabled.png",import.meta.url).href,
          },
          {
            value:'标注',
            label:'标注',
            img:new URL("../../../assets/images/etop/tool/icon_collect.png",import.meta.url).href,
            disableImg:new URL("../../../assets/images/etop/tool/icon_collect_disabled.png",import.meta.url).href,
          },         
          {
            value:'收藏',
            label:'收藏',
            img:new URL("../../../assets/images/etop/tool/icon_collect.png",import.meta.url).href,
            disableImg:new URL("../../../assets/images/etop/tool/icon_collect_disabled.png",import.meta.url).href,
          },
      ]
    }
  },
  props:{
    isToolBoxShow: {
      type:Boolean,
      default: true,
    },
    outsideTool:{
      type:Array,
      default:()=>{
        return ['放大','缩小','全屏','重置',"全量服务菜单","收藏"]
      }
    },
    boxToolOption:{
      type:Array,
      default:()=>{
        return ['测距','场景控制']
      }
    },
    disableTool:{
      type:Array,
      default:()=>{
        return ['全量服务菜单']
      }
    },
    baseLayerName:String,
  },
  mounted(){
    this.outsideOption = this.toolOption.filter(item=>{
      return this.outsideTool.indexOf(item.value) !== -1
    })

    this.toolboxOption= this.toolOption.filter(item=>{
      return this.boxToolOption.indexOf(item.value) !== -1
    })

    if(!this.isToolBoxShow){
      document.getElementById('baseLayerSwitcher').style.right = '50px'
      if(document.getElementById('region_and_search').className == 'tools'){
        document.getElementById('region_and_search').style.right = '160px'
      }else if(document.getElementById('region_and_search').className == 'activeTools'){
        document.getElementById('region_and_search').style.right = '220px'
      }
    }
  },
  created(){

  },
  methods:{
    init(map){
      this.map = map
    },
    toggle(){
      this.isToggle = !this.isToggle
    },
    toolChange(val){
      if(!val){
        //清空操作
        switch(this.lastVal){
          case '测距':
            //清除测量结果
            this.clearMeasure()
            break;
          case '锁定垂直视角':
            //取消锁定垂直视角
            this.setPitch()
            break;
          case '全屏':
            this.fullScreen()
            break;
        }
      }
      //如果有禁用的 则不执行
      if((this.disableTool.indexOf(val) !==-1))  return
      switch(val){
        
        case '测距':
          this.measureLength()
          break;
        case '标注':
          break;
        case '全屏':
          this.fullScreen()
          break;
        case '重置':
          this.reset()
          break;
        case '锁定垂直视角':
          this.setPitch()
          break;
        case '场景控制':
          break;
        case '全量服务菜单':
        
          this.toggleFullService()
          break;
        case '放大':
          this.zoomMap(val)
          break;
        case '缩小':
          this.zoomMap(val)
          break;
      }
      //记录上次的值
      this.lastVal = val
    },
    zoomMap(val){
      if (!window.etopMap.map) return
      if(val=='放大'){
        let arr = document.querySelector('#distance-legend-tool > div > div.distance-legend-text').innerText.split(' ');
        if (Number(arr[0]) <= 10 && arr[1] == 'm') return
        window.etopMap.map.zoomIn()
      }else{
        window.etopMap.map.zoomOut()
      }
    },
    updateRegionSwitcher(res) {
      this.$emit('updateRegionSwitcher', res)
    },
    toggleFullService(){
      this.isLayerControlShow = !this.isLayerControlShow
      this.$emit('toggleFullService',this.isLayerControlShow)
    },
    // 点击全屏时触发
    fullScreen() {
      if(Cesium.Fullscreen.fullscreen){
        this.$emit('exitFullscreen')
        window.Cesium.Fullscreen.exitFullscreen()
        this.isFullScreen = false
      }else{
        this.$emit('fullScreen')
        this.isFullScreen = true
      }
    },
    // 重置地图
    reset() {
      this.$emit('reset')
      if (!this.$parent.isSZ) 
        this.$parent.toogleSS()
    },
    // 回到当前位置
    resetLocation() {
      this.$emit('resetLocation')
    },
    setPitch(){
      this.pitchEnabled = !this.pitchEnabled
      this.$emit('setPitch', this.pitchEnabled)
    },
    //空间距离
    measureLength(){ 
      //测量对象
      if(!window.etopMap.marsMeasure){
        window.etopMap.marsMeasure = new mars3d.thing.Measure({
          label: {
            color: "#ffffff",
            font_family: "楷体",
            font_size: 20,
            background: false
          }
        })  
        this.map.addThing(window.etopMap.marsMeasure)
      }

      //判断当前baselayer类型
      if(this.baseLayerName == '电子地图' || this.baseLayerName == '卫星影像' || this.baseLayerName == '蓝色版'){
        //二维场景 测空间距离
        window.etopMap.marsMeasure.distance({
          showAddText: true
        })
      }else {
        //三维场景 测高度
        window.etopMap.marsMeasure.height()
      }   
    },
    clearMeasure(){
       window.etopMap.marsMeasure.clear()
    }
  }
}
</script>

<style lang="less">
@import url(../../../assets/styles/main.less);
  .label {
    overflow:hidden;
    text-overflow:ellipsis;
    white-space:nowrap;
  } 
</style>
<style lang="less" scoped>
.ytools {
  .menu {
    width: 100px;
    // height: 32px;
    padding: 5px 5px 5px 8px;
    color: #54b5ff;
    border: 1px solid rgba(84, 181, 255, 0.3);
    background-color: rgba(11, 36, 68, 0.8);
    cursor: pointer;
    .down_arrow {
        position: absolute;
        right: 12px;
        top: 12px;
        width: 0;
        height: 0;
        border-left: 8px solid transparent;
        border-right: 8px solid transparent;
        border-top: 8px solid #54b5ff;
    }
}

 .menu_box {
    width: fit-content;
    margin-top: 8px;
    position:absolute;
    right:0px;
    display: flex;
    flex-direction: column;
    transition: all 1s;
    flex: 1;
  
    white-space: nowrap;
    .menu_item{
      margin-bottom:5px ;
      cursor: pointer;
    }
  }
  .toolbox {
    position:absolute;
    top:10px;
    right:50px;
    height:30px;
    .select-popper {
      z-index: 99;
      // height: 34px;
      width: 100px;
      //margin-top:10px;
      margin-left: 15px;
      .down_arrow {
        position: absolute;
        right: 0px;
        top: 4px;
        width: 0;
        height: 0;
        border-left: 8px solid transparent;
        border-right: 8px solid transparent;
        border-top: 8px solid #54b5ff;
      }
    }
    .ant-select-clear {
      background:transparent;
      .closeIcon {
        background-image: url("../../../assets/images/etop/tool/blue-close.png");
      }
    }
  }
  .outsidetool {
    position:absolute;
    top:10px;
    right:10px;
    text-align: right;
    .outsideTool {
      margin-bottom:10px;
      cursor:pointer;
    }
  }
}

</style>