<template>

  <div v-if="isLayerControlShow" class="layerControl">
    <div class="header">
    <span>全量服务菜单</span>
    <span @click="expandAll">
      <span class="expand" v-if="!isExpandAll">展开全部</span>
      <span class="expand" v-else>折叠全部</span>
      <img :src="expandimg1" v-if="!isExpandAll">
      <img :src="expandimg2" v-else>
    </span>
  </div>
  <!-- 图层目录树 -->
    <a-tree
    checkable
    class="elTree"
    v-model:checkedKeys="checkedLayers"
    v-model:expandedKeys="expandedKeys"
    :tree-data="currentLayers"
    :key="{key}"
  >
    <template #title="{ dataRef}">
     {{  dataRef.title }}
     <img v-if="dataRef.is3D" :src="is3Dimg" alt="">
    </template>
  </a-tree>
  </div>

</template>

<script>
import testData from './layerControlTestData.js'

/**
   * @module LayerControl
   * @desc LayerControl组件,图层控制组件
   */

export default{
  data(){
    return {
      checkedLayers:[],   //选中的节点key
      currentLayers:[],  //图层控制的节点  数据格式见layerControlTestData.js
      expandedKeys:[],  //展开的节点key
      //defaultExpandedKeys:['3dTerrain','DOM'],  //测试默认展开节点
      testData,    //图层控制数据 从配置文件中导入, 包括节点数据和图层配置
      isExpandAll: false,   //是否展开节点
      expandimg1:new URL("./images/icon-layer-expand.png",import.meta.url).href,
      expandimg2:new URL("./images/icon-layer-unexpand.png",import.meta.url).href,
      is3Dimg:new URL("./images/icon_3D.png",import.meta.url).href,
    }
  },
  props:{
    defaultExpandedKeys:{
      type:Array,
      default:()=>{
        return ['3dTerrain','DOM']
      }
    },
    isLayerControlShow:{
      type:Boolean,
      default: false,
    }
  },
  watch:{
    checkedLayers:{
      deep:true,
      handler(n,o){
        //方式一 全部图层重新控制显隐
        // const checkedId = Object.values(n)
        // this.currentLayers.forEach(layer=>{
        //   const index = checkedId.indexOf(layer.key)
        //   const curlayer = this.map.getLayerById(layer.key)
        //   if(curlayer){
        //     if(index==-1){
        //       this.map.getLayerById(layer.key).show = false
        //     }else {
        //       this.map.getLayerById(layer.key).show = true
        //     }
        //   }
        // })
      
        //方式二 判断checkedLayers增加/减少了哪个图层
        if(n.length > o.length){
          console.log('增加了')
          Object.values(n).filter(item=>Object.values(o).indexOf(item)==-1).forEach(layerId=>{
            if(this.map.getLayerById(layerId)){
              //若图层存在, 设置图层显示
              this.map.getLayerById(layerId).show = true
            }else{
              //若图层不存在, 从配置文件查找图层配置, 加载图层
              try{
                window.etopMap.layer.loadLayer(this.testData[layerId])
              }catch(error){
                alert('缺少图层配置')
                this.checkedLayers = o
              }
            }
          })
        }else if(n.length < o.length){
          console.log('减少了')
          Object.values(o).filter(item=>Object.values(n).indexOf(item)==-1).forEach(layerId=>{
            if(this.map.getLayerById(layerId)){
              //若图层存在, 设置图层隐藏
              this.map.getLayerById(layerId).show = false
            }else{
              //若图层不存在,
            }
          })
        }
      }
    },
  },
  mounted(){
    //测试节点数据
    const testNodes = this.testData.treeData
    this.currentLayers = testNodes
    //测试默认展开节点
    this.expandedKeys = this.defaultExpandedKeys
  },
  methods:{
    /** 地图初始化完成调用, 用来获取map对象
     * params: {Object} map 地图对象
     */
    init(map) {
      this.map = map;
      //this.addListener()
    },
    /**
     * 全部展开/折叠(恢复默认展开)的方法
     */
    expandAll(){
      //展开状态取反
      this.isExpandAll = !this.isExpandAll
      this.expandedKeys = []
      if(this.isExpandAll){
        //展开全部
        this.expandLoop(this.currentLayers, this.expandedKeys)
      }else {
        //恢复默认展开
        this.expandedKeys = this.defaultExpandedKeys
      }
    },
    /**
     * 递归函数, 用来获取每个子节点的key,
     * params: {Array} target 要递归展开的数组
     * params: {Array} result 存放结果的数组
     */
    expandLoop(target, result){
      target.forEach(item=>{
        result.push(item.key)
        item.children && this.expandLoop(item.children, result)
      })
    },
    /**
     * map对象监听渲染事件postRender, 动态获取当前加载的图层, 渲染到图层树
     * 当前弃用
     */
    addListener(){
      //添加map监听事件(添加图层/移除图层/图层显示/图层隐藏) 
      //'addLayer','removeLayer'
      this.map.on('postRender',()=>{
        this.currentLayers = []
        this.checkedLayers = []
        this.map.getLayers().forEach(layer=>{
           this.currentLayers.push({
             key: layer.options.id,
             title: layer.options.name || layer.options.id,
           })
           if(layer.show){
             this.checkedLayers.push(layer.options.id)
           }
        })
      })
    }
  }
}
</script>

<style lang="less" scoped>
  .controlButtons {
    position:absolute;
    top:10px;
    left:10px;
    cursor:pointer;
  }
  .layerControl{
    position:absolute;
    top:60px;
    right:60px;
    color:#fff;
    background-color: #0d1c45;
    border: 1px solid #305e95;
    .header {
      height:40px;
      width: 400px;
      border-bottom:1px solid #305e95;
      padding:5px 10px;
      font-size:18px;
      .expand {
        position:relative;
        right:-200px;
        font-size:14px;
        border:1px solid #4283c3;
        padding:4px;
      }
      img {
        position:relative;
        right:-210px;
      }
    }
    .elTree {
      color:#fff;
      width: 400px;
      height:700px;
      background-color: #040b34;
      padding:10px;
      max-height:700px;
      overflow-y: auto;
      overflow-x: hidden;
      &::-webkit-scrollbar {
        width: 5px;
      }
      &::-webkit-scrollbar-thumb {
        opacity: 0.7;
        background: #3481df;
        border-radius: 5px;
      }
    }
  }
</style>
<style lang="less" >

.layerControl {
  .ant-tree li .ant-tree-node-content-wrapper {
    color:#fff !important;
  }
  .ant-tree li .ant-tree-node-content-wrapper:hover {
    background-color: transparent !important;
  }
  .ant-tree .ant-tree-checkbox-inner {
    background-color: transparent !important;
    border:none !important;
  }
  .ant-tree-title {
    position:relative;
    left:-20px;
  }
  .ant-tree-checkbox-checked::after {
    border:none !important;
  }
  //父节点展开样式 不显示
  .ant-tree-checkbox-indeterminate .ant-tree-checkbox-inner::after{
    display: none;
  }
   //未选中样式
  .ant-tree-switcher-noop + .ant-tree-checkbox {
    .ant-tree-checkbox-inner::after {
      background-image: url("./images/icon_checkbox_nor.png") !important;
      width:14px !important;
      height: 14px !important;
      top:0 !important;
      left:-15px !important;
      border: none !important;
      transform:none !important;
      opacity: 1 !important;
    }
  }
  //选中样式
  .ant-tree-switcher-noop + .ant-tree-checkbox-checked {
    .ant-tree-checkbox-inner::after {
      background-image: url("./images/icon_checkbox_sel.png") !important;
      background-color: transparent !important;
      width:14px !important;
      height: 14px !important;
      box-sizing:border-box !important; 
      transform: none !important;
      top:0 !important;
      left:-15px !important;
    }
  }
  .ant-tree li span.ant-tree-switcher {
    top:5px !important;
  }
  .ant-tree-switcher_close {
    background-image: url("./images/icon-layer-expand.png") !important;
    background-repeat: no-repeat;
  }
  .ant-tree-switcher_open {
    background-image: url("./images/icon-layer-unexpand.png") !important;
    background-repeat: no-repeat;
  }
  .ant-tree li span.ant-tree-switcher.ant-tree-switcher_open .ant-tree-switcher-icon,
  .ant-tree li span.ant-tree-switcher.ant-tree-switcher_close .ant-tree-switcher-icon{
      display:none !important;
    }
}
</style>
